import Alert from "@mui/material/Alert";

//export default function getData(ticker) {


function minTwoDigits(n) {
    return (n < 10 ? '0' : '') + n;
}

// var d1_prices;

export const callAPI = async (ticker) => {

    var out_prices;
    var out_time;
    var out_Array = new Array(0);

    const url = 'https://www.alphavantage.co/query?function=TIME_SERIES_INTRADAY&symbol=' + ticker + '&interval=1min&outputsize=full&apikey=DEMO'//SJYFLRUSEJL7QMAA'


    try {
        const res = await fetch(url);
        const data = await res.json();

        const categories = Object.values(data);
        const dates = Object.keys(categories[1]);
        const prices = Object.values(categories[1]);

        const values = JSON.stringify(prices);

        var open = values.split(/[{}]+/);

        var filtered = open.filter(function (el, index) {
            // normally even numbers have the feature that number % 2 === 0;
            // JavaScript is, however, zero-based, so want those elements with a modulo of 1:
            return index % 2 === 1;
        });

        for (let i = 0; i < filtered.length; i++) {
            var temp = filtered[i].split('\"');
            filtered[i] = temp[3];

        }

        const hours = new Array();

        const combined = new Array();

        combined.push(dates[0].substring(0, 4) + dates[0].substring(5, 7) + dates[0].substring(8, 10))

        for (let i = 0; i < dates.length; i++) {
            //  hours.push(dates[i].substring(11, 13));
            //  minutes.push(dates[i].substring(14, 16));
            let minutes = (dates[i].substring(14, 16));


            minutes = parseInt((minutes / 60) * 100);
            minutes = minTwoDigits(minutes);

            //   let daystamp = dates[i].substring(0, 4) + dates[i].substring(5, 7) +dates[i].substring(8, 10)



            combined.push(dates[i].substring(11, 13) + "." + minutes);
            // console.log(combined[i]);
            //out_time.push(hours[i] + "." + parseFloat(minutes[i]));
        }
        /* for (let i = 0; i < dates.length; i++) {
            
             out_time.push(hours[i] + '.' + (minutes[i]));
         }*/


        var lastday = dates[0].substring(8, 10);


        var daycounter = 0;
        var d1_pointer = 0;


        for (let i = 0; i < dates.length; i++) {

            const currentday = dates[i].substring(8, 10);

            if (parseInt(currentday) != parseInt(lastday)) {
                lastday = currentday;
                daycounter++;

                if (daycounter == 1) {
                    d1_pointer = i;
                    break;
                }


            }
        }



        out_prices = filtered.slice(0, d1_pointer);
        out_time = combined.slice(0, d1_pointer);

        out_prices.unshift(0);

        for (let i = 0; i < d1_pointer; i++) {
            //console.log(out_time[i])
            out_Array.push({ x: parseFloat(out_time[i]), y: parseFloat(out_prices[i]) })
        }
     /*   return (
            <div>

                <Alert severity="success">Loaded successfully!</Alert>
            </div>

        );*/
        //console.log(out_Array)

    } catch (err) {
       /* return (
            <Alert severity="error">API request failed: {err.messages}</Alert>
        );*/


     //   console.log(err);
    }



    return out_Array;
};

